import styled, { css } from "styled-components";

export const Icon = styled.span.attrs({ role: "img" })`
  ${({ small }) =>
    !small &&
    css`
      font-size: 125%;
    `}
  padding-right: 0.75em;
`;

/* TODO: Use aria-labelledby (in calling component) for i18n support (this is linted for 'emoji' unicode chars),
but this requires https://github.com/facebook/react/issues/1137 */

// à la :before {content: '🏠';}
export const HomeIcon = styled(Icon).attrs({ children: "🏠" })``;
export const TelephoneIcon = styled(Icon).attrs({ children: /* "📞" */ "☎" })``;
export const EmailIcon = styled(Icon).attrs({ children: "✉" })``;
export const CopyrightIcon = styled(Icon).attrs({ children: "©" })``;

