import { createElement as $ } from "react";
import styled, { css } from "styled-components";
import { wrapIfNeeded } from "../common/react-utils";

export const Item = styled.span`
  display: ${({ innerDisplay }) => `inline-${innerDisplay || "block"}`};
  vertical-align: middle;
  width: ${({ portion }) => portion * 100}%;
  text-align: ${({ align }) => align};
  ${({ align, gap }) => css`
    padding-${align}: ${gap}
  `};
  margin-right: ${({ overlap }) => overlap && `${-overlap * 100}%`};
`;

/**
 * return an 'aligner' that takes current position `i` and total length `n`
 */
export const alignerByJustify = (justify) => {
  switch (justify) {
    case "center":
      return (i, n) => (i === 0 ? "right" : i === n - 1 ? "left" : "center");
    case "spaceBetween":
      return (i, n) => (i === 0 ? "left" : i === n - 1 ? "right" : "center");
    case "spaceAround":
      return () => "center";
    default:
      return () => "left";
  }
};

/**
 * Mock css grid `justify-content: center;` and more
 */
const align = (componentType, { justify, gap }) => ({ children, ...props }) => {
  const aligner = alignerByJustify(justify),
    numItems = children.length;
  return $(
    componentType,
    props,
    // React.Children.map(children, (child, i) => probably safer for single child ?
    ...children.map((child, i) =>
      $(
        Item,
        {
          portion: 1 / numItems,
          align: aligner(i, numItems),
          gap,
        },
        child
      )
    )
  );
};

export default align;
export const center = (cT, opts) => align(cT, { justify: "center", ...opts });

/**
 * Use with care: like `align` with `justify :spaceBetween`, but items may interleave.
 * You can provide your own `Item` as child, no additional wrapper will be created in that case.
 */
export const overlap = (componentType) => ({ children, ...props }) => {
  const aligner = alignerByJustify("spaceBetween"),
    numItems = children.length;
  return $(
    componentType,
    props,
    ...children.map((child, i) =>
      wrapIfNeeded(child, Item, {
        portion: i === 0 ? 1 : 1 / numItems,
        align: aligner(i, numItems),
        overlap: i === 0 && 1 - 1 / numItems,
      })
    )
  );
};
