/******** Table Layout ********/

const TableStyle = styled.div`
  display: table;
  width: ${({ fullWidth }) => fullWidth && `100%`};
  /* Use padding instead to achieve styles that work for tables or normal divs */
  /* ${({ spacingEx }) => spacingEx && `border-spacing: ${spacingEx}ex;`} */
`;

/* cfr. justify-content/Item */
export const CellLike = styled.div.attrs(({ asListItem }) => ({
  as: asListItem && ListItemStyle,
}))`
  display: table-cell;
  text-align: ${({ align }) => align};
  ${({ align, gap }) => css`
    padding-${align}: ${gap}
  `};
`;

const RowStyle = styled.div`
  display: table-row;
`;

export const RowLike = ({ children, _gap, _aligner, _asList, ...props }) => {
  if (!_aligner)
    console.error("RowLike must be called from within a `Table` component!");
  const numItems = children.length;
  return $(
    RowStyle,
    props,
    ...Arrays.wrap(children).map((child, i) =>
      wrapIfNeeded(child, CellLike, {
        gap: _gap,
        align: _aligner(i, numItems),
        asListItem: _asList && i === 0,
      })
    )
  );
};

// TODO: if (!children.every(child => child instanceof Row) / create single row wrapper ?
/**
 * Cells should be grouped into `RowLike` items.
 * You can also provide your own `CellLike`, no redundant wrappers will be created.
 * @param {*} param0 props
 */
export const TableLike = ({ justify, gap, asList, children, ...props }) => {
  const aligner = alignerByJustify(justify);
  return $(
    TableStyle,
    { as: asList && ListStyle, ...props },
    ...children.map((child) =>
      cloneElement(child, { _gap: gap, _aligner: aligner, _asList: asList })
    )
  );
};

export const InlineTableLike = styled(TableLike)`
  display: inline-table;
`;
