import React, { createElement as $, useEffect, useRef } from "react";
import SwipeableViews from "react-swipeable-views";
import Navigation from "../components/Navigation/Navigation";
import { GlobalStyle } from "../site-styles";
import {
  getIndex,
  getScreens,
  isDefaultScreen,
  resolve,
} from "../site/Screens";
import { Main } from "./app-styles";
import Footer from "./Footer";
import Header from "./Header";
import useRoutingAndScroll from "./routing-scroll.hook";
import Screen from "./Screen";

/* disable buggy scroll-restoration: auto behaviour: when body.overflow is 'hidden',
   it will stop working whenever a non-overflowing page is hit.
   Therefore we hereby declare that we want to handle scroll restoration ourselves (see `useRoutingAndScroll`)
   see also https://developers.google.com/web/updates/2015/09/history-api-scroll-restoration
*/
if ("scrollRestoration" in window.history) {
  window.history.scrollRestoration = "manual";
}

// swipeable-views issue? bindKeyboard makes animation not work!
const MySwipeableViews = /* bindKeyboard( */ SwipeableViews; /* ) */

function App() {
  const [screen, switchScreen] = useRoutingAndScroll();
  // TODO: useLayoutEffect ?
  useEffect(() => {
    const titlePostfix = isDefaultScreen(screen.id) ? "" : `: ${screen.title}`;
    document.title = `Italienisch Lernen – Debora Difonzo${titlePostfix}`;
  }, [screen]);

  return (
    <>
      <GlobalStyle />
      <Main>
        <Header />
        {$(ScreenView, { screen, switchScreen })}
        <Footer onSwitchScreen={switchScreen} />
      </Main>
    </>
  );
}

export default App;

function ScreenView({ screen, switchScreen }) {
  const screens = getScreens(screen);
  const children = screens
    /* FIXME: 2nd map arg. ix interpeted as history map. -> 5[0] === void TODO: ScreenHistory? */
    .map(resolve)
    .map(({ id, clazz, ...opts }, ix) =>
      $(
        Screen,
        { key: id, screenid: id },
        // TODO: wozu `ix`, wahrscheinlich fuer aforismi etc. Kann man das dann sauberer lösen, vllt. über `opts`
        $(clazz, { ix, onSwitch: switchScreen, onHeightChange, ...opts })
      )
    );
  const index = getIndex(screen);
  /******  Workaround for https://github.com/oliviertassinari/react-swipeable-views/issues/535
   * Alternative with exported SwipeableViewsContext (maybe more future proof variant as apparently planned for documentation):
   * https://github.com/oliviertassinari/react-swipeable-views/blob/v0.14.0/docs/src/pages/demos/DemoAnimateHeight.js#L44-L45
   *   ******/
  const ref = useRef();
  function onHeightChange() {
    ref.current && ref.current.getSwipeableViewsContext().slideUpdateHeight();
  }
  /******  .  ******/
  return (
    <>
      <Navigation activeScreen={screen} onSwitch={switchScreen} />
      <MySwipeableViews
        ref={ref}
        /* TODO: Workaround for a bug where indices in swipeableViews render function are wrong,
        happens exactly when switching the screens array.
        The workaround causes a remount and therefore we're losing the animation. :/ */
        key={screens}
        /* . */
        index={index}
        onChangeIndex={switchScreen}
        enableMouseEvents
        animateHeight // makes a few workarounds necessary (see above)
        resistance
      >
        {children}
      </MySwipeableViews>
    </>
  );
}
