import styled, { css } from "styled-components";

export const NavBar = styled.nav`
  display: table;
  border-collapse: collapse;
  width: 100%;
  text-align: center;
  font-family: "Raleway";
  @media (min-width: 768px) {
    table-layout: fixed;
  }
`;

export const NavLink = styled.a.attrs(({ active }) => ({
  tabIndex: active ? -1 : null,
}))`
  display: table-cell;
  padding: 3ex 1ex;
  font-weight: 800;
  cursor: pointer;
  border-width: 0.2ex;
  border-style: solid;
  border-color: hsl(18, 43%, 15%);
  overflow: hidden;
  text-overflow: ellipsis;
  background: linear-gradient(hsl(18, 43%, 25%), hsl(18, 43%, 20%));
  color: #fff;
  opacity: 0.75;
  text-decoration: none;
  position: relative; /* for z-index below */
  :hover {
    z-index: 1;
    overflow: initial;
    opacity: 0.85;
  }
  :active,
  :focus {
    background: linear-gradient(hsl(18, 43%, 20%), hsl(18, 43%, 25%));
  }
  :focus {
    outline: none;
  }
  :disabled {
    cursor: default;
  }
  ${({ active }) =>
    active &&
    css`
      color: hsl(34, 74%, 67%); /* REF .site--text-primary */
      /* :disabled behaviour */
      cursor: default;
    `}
`;
