import { createGlobalStyle } from "styled-components";

import bgImg from "./img/bg.jpg";

export const GlobalStyle = createGlobalStyle`
/* https://www.paulirish.com/2012/box-sizing-border-box-ftw/ */
html {
    box-sizing: border-box;
  }
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  
  /* . */
  body {
    padding: 0;
    overflow-y: hidden;
  }
  
  html,
  body,
  #root {
    height: 100%;
    margin: 0;
  }
  
  /* By TAG type */
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-top: 0;
    margin-bottom: 0.5rem;
  }

  #root {
    /* Controls the majority of lengths on the site. Capped at [iPod: 1024x768] via @media (see below) */
    font-size: 2.5vmin;
    overflow-y: scroll;
    background: /* linear-gradient(rgba(47, 23, 15, 0.65), rgba(47, 23, 15, 0.65)), */ url(${bgImg});
    background-position: center;
    background-size: cover;
  }
  @media (min-width: 768px) {
    #root {
      font-size: 19.2px;
    }
  }
  
  em {
    font-size: 112.5%;
  }
`;
