import { createElement as $ } from "react";

import { NavBar, NavLink } from "./nav-styles";
import { getScreens } from "../../site/Screens";

/** *PROPS*
 *  * `active`: one of the keys in `Screens`
 *  * `onSwitch`: callback
 */
const Navigation = ({ activeScreen, onSwitch }) => {
  const { id: activeId } = activeScreen;
  const navScreens = getScreens(activeScreen, true);
  return $(
    NavBar,
    null,
    navScreens.map(({ id, title }) => {
      const active = activeId === id;
      return $(
        NavLink,
        {
          key: id,
          href: `${id}.html`,
          "data-screenid": id,
          active,
          onClick: onSwitch,
          onKeyPress(e) {
            if (e.key === "Enter") onSwitch(e);
          },
        },
        title
      );
    })
  );
};

export default Navigation;
