import styled from "styled-components";

const SCREEN_PADDING = "6vmin";

const isDiary = (id) => id.startsWith("/diary/");

const Screen = styled.div`
  position: relative; /* NOTE: for stemma-left etc. */
  padding: ${({ screenid }) => !isDiary(screenid) && SCREEN_PADDING};
  background-color: rgba(255, 255, 255, 0.85);
`;

export default Screen;
