import { createElement as $, cloneElement } from "react";
/**
 * wrap a React component in a component of type `componentType`, passing the `opts`.
 *
 * The function is smart enough not to rewrap components that are already of the desired type.
 */
export function wrapIfNeeded(component, componentType, opts) {
  if (isInstanceOf(component, componentType)) {
    return cloneElement(component, opts);
  }
  return $(componentType, opts, component);
}

/**
 * Requires the following pattern as long as there is no built-in instanceof in styled-components:
 * ```
 * const MyCell = styled(Cell).attrs({ superType: Cell })`
    font-size: 112.5%;
    padding-left: 3ex;
    `;
    ```
    (now `isInstanceOf(<MyCell />,Cell)` returns `true`)
 */
function isInstanceOf(cmp, type) {
  let curType = cmp.type;
  if (!curType) return false;
  do {
    if (curType === type) return true;
    // TODO: Where does attrs really come from? is it always present in styled component types?
    const attr = (curType.attrs || []).find((attr) => attr.superType);
    if (attr) {
      // a super type present, keep searching
      curType = attr.superType;
    } else break;
  } while (true);
}
