import Home from "../screens/Home";
import Kurse from "../screens/Kurse";
import Kontakt from "../screens/Kontakt";
import Lehrperson from "../screens/Lehrperson";
import Programm from "../screens/Programm";
import Events from "../screens/Events";
import Impressum from "../screens/Impressum";
import Datenschutz from "../screens/Datenschutz";
import Diary from "../screens/diary/Diary";
import Aforismi from "../screens/diary/Aforismi";
import { aforismi } from "./diary/aforismi";

const ROOT = {
  name: "",
  title: "Hauptseite",
  screens: [
    { name: "Home", clazz: Home, title: "Home" },
    { name: "Lehrperson", clazz: Lehrperson, title: "Lehrperson" },
    { name: "Kurse", clazz: Kurse, title: "Kurse" },
    { name: "Programm", clazz: Programm, title: "Programm" },
    { name: "Events", clazz: Events, title: "Events" },
    { name: "Kontakt", clazz: Kontakt, title: "Kontakt" },
    {
      name: "Impressum",
      clazz: Impressum,
      title: "Impressum",
      legal: true,
      hidden: true,
    },
    {
      name: "Datenschutz",
      clazz: Datenschutz,
      title: "Datenschutzerklärung",
      legal: true,
      hidden: true,
    },
    {
      name: "diary",
      title: "Diary",
      hidden: true,
      screens: [
        { name: "Willkommen", title: "Diary", clazz: Diary },
        {
          name: "Aforismi",
          title: "Aforismi",
          screens: aforismi.map((aforismo) => ({
            name: aforismo.title.replace(/ /g, "_"),
            clazz: Aforismi,
            title: aforismo.title,
            content: aforismo,
          })),
        },
        { name: "Pensieri", clazz: () => "Pensieri", title: "Pensieri" },
        { name: "Lessico", clazz: () => "Lessico", title: "Lessico" },
      ],
    },
  ],
  *[Symbol.iterator]() {
    const stack = [this];
    let screen = null;
    while ((screen = stack.pop())) {
      const { screens = [] } = screen;
      // add in reverse order for the natural DFS order
      let i = screens.length;
      while (--i >= 0) {
        stack.push(screens[i]);
      }
      yield screen;
    }
  },
};
/* Set Parent pointers */
{
  function setParent(screens, parent) {
    for (let i = 0; i < screens.length; ++i) {
      const screen = screens[i];
      screen._parent = parent;
      if (screen.screens) {
        setParent(screen.screens, screen);
      }
    }
  }
  setParent([ROOT], null);
}
/* set id and HASH access */
const HASH = {};
{
  function setHash(screen, prefix) {
    if (screen.name) prefix += "/" + screen.name;
    screen.id = prefix;
    HASH[prefix] = screen;
    if (screen.screens) {
      for (const subScreen of screen.screens) {
        setHash(subScreen, prefix);
      }
    }
  }
  setHash(ROOT, "");
}

/* Determine subscreens for swiping/nav */
for (const screen of ROOT) {
  const { screens, _parent } = screen;
  if (screens) {
    // hide hidden screens when on a visible screen
    screen._screensV = screens.filter(({ hidden }) => !hidden);
    // hide subscreens that cause a change of the screen list to avoid confusion
    screen._screensH = screens.filter(({ screens }) => !screens);
    if (_parent) {
      /* Add a back link to parent, but avoid making ROOT cyclic */
      screen._screensV.unshift(_parent);
      screen._screensH.unshift(_parent);
    }
  }
}

/* Some invariants and requirements:
 * - `screen._parent` is always virtual
 * - cross-references are always to virtual screens (currently only back-reference to direct parents used/tested)
 * - _default_ screen for a virtual screen is the first descendant proper screen (without following back references).
 *   (Rationale: Blog's first entry is a back reference to / -> /Home,
 *    not certainly part of the Blog and also causes duplicate-key issues)
 * - Each virtual screen needs at least 1 non-virtual subscreen, otherwise it will be 'skipped' in nav-generation
 *   (TODO: Additionally, it must precede all virtual sub-screens?)
 */
Object.freeze(ROOT);
Object.freeze(HASH);

/**
 * screen
 * @param {string} id .
 */
export function getScreen(id) {
  return HASH[id];
}

function isConcrete({ screens }) {
  return !screens;
}

/**
 * resolves virtual screens with their default screen as defined above
 * @param {string} id screen id
 * @param {object} [backup] hash holding a history of ids per virtual screen
 */
export function resolveScreen(id, backup) {
  return resolve(getScreen(id), backup);
}

/**
 *
 * @param {*} screen
 * @param {*} backup
 */
export function resolve(screen, backup) {
  return isConcrete(screen)
    ? screen
    : resolve(
        (backup && getScreen(backup[screen.id])) ||
          /* `screen.screens[0]` might be a back reference (see above) */
          screen.screens.find((subScreen) => isSubScreen(subScreen, screen))
      );
}

function slashCount(s) {
  return (s.match(/\//g) || []).length;
}

function isSubScreen({ id }, { id: parentId }) {
  return slashCount(id) > slashCount(parentId);
}

const _resolvedRoot = resolve(ROOT);
/**
 * whether on landing page
 * @param {*} id .
 */
export function isDefaultScreen(id) {
  return getScreen(id) === _resolvedRoot;
}

/**
 * Resolves an arbitrary string to a valid screen (landing page if no match)
 *
 * virtual screen ids are correctly resolved
 * @param {string} id .
 */
export function resolveScreenOrDefaultId(id) {
  const screen = getScreen(id);
  return (screen ? resolve(screen) : _resolvedRoot).id;
}

/**
 * Screens for swiping
 * @param {*} screen a _proper_ screen
 * @param {*} hideHidden always hide hidden screens (even if itself hidden)
 */
export function getScreens({ _parent, hidden }, hideHidden = !hidden) {
  return hideHidden ? _parent._screensV : _parent._screensH;
}

/**
 * swiping index of the screen in the specified screen-set.
 *
 * TODO: Cache the indices
 * @param {object} screen screen
 */
export function getIndex(screen) {
  return getScreens(screen).indexOf(screen);
}

export const legalScreens = Array.from(ROOT).filter(({ legal }) => legal);
