import { Children, createElement as $, forwardRef } from "react";
import styled, { css } from "styled-components";
import { Arrays } from "../common/utils";

/******** Paragraphs ********/

export const Paragraph = styled.div`
  line-height: 1.75;
  margin-top: 1em;
  ${({ hasFloat }) =>
    hasFloat &&
    css`
      :after {
        content: "";
        display: block;
        clear: both;
      }
    `}
  /* TODO: replace by css-props */
  min-height: ${({ minHeight }) => minHeight};
`;

const ListParagraphStyle = styled(Paragraph)`
  display: inline-block;
  text-align: left;
`;

export const ListParagraph = styled(ListParagraphStyle).attrs({ as: "ul" })``;

export const Small = styled.div`
  font-size: 85%;
`;

export const Indented = styled.div`
  padding-left: 2ex;
`;

/******** Lists ********/

export const List = forwardRef(({ indented, children, ...props }, ref) =>
  $(
    "ul",
    { ref, ...props },
    Children.map(children, (child) =>
      indented ? $(Indented, { as: "li" }, child) : $("li", null, child)
    )
  )
);

export const ListStyle = styled.div`
  margin-top: 1em;
`;
export const ListItemStyle = styled.div`
  /* display: list-item; Not useful because the bullet is 'hanging' */
  padding-left: 2ex;
  &:before {
    content: "•";
    padding-right: 1ex;
  }
`;
export const ListLike = forwardRef(({ children, ...props }, ref) =>
  $(
    ListStyle,
    { ref, ...props },
    Children.map(children, (child) => $(ListItemStyle, null, child))
  )
);

