export const FootnoteMarker = styled.span`
  margin-left: 1ex;
  cursor: pointer;
  color: hsl(214, 74%, 33%);
  :before {
    content: "*";
  }
`;

/* TODO: Use cssprop in these cases! see styled-components/macro comments */
const FootnotesStyle = styled.div`
  margin-top: 2em;
`;
/**
 * You can provide your own <Footnote>, no additional wrappers will be created in that case
 */
export const Footnotes = forwardRef(({ children, ...props }, ref) =>
  $(
    FootnotesStyle,
    { ref, ...props },
    ...Arrays.wrap(children).map((child) => {
      return child.type === Footnote ? child : $(Footnote, null, child);
    })
  )
);
export const Footnote = styled.p`
  font-size: 85%;
  :before {
    content: "(*)";
    padding-right: 1ex;
  }
`;
