/* ******** Routing/Scroll Hook ******** */

import { useState, useEffect } from "react";
import {
  resolveScreen,
  getScreen,
  isDefaultScreen,
  getScreens,
  resolveScreenOrDefaultId,
} from "../site/Screens";

const rootElement = document.getElementById("root");

// TODO: useRef() instead of globals for `scrollHeights` and `ScreenHistory` ? Or can we just require/enforce singleton use?
// A Map screenId -> scrollTop. push/Popstate would be slicker, but popstate does not give previous state.
const scrollHeights = {};

const ScreenHistory = {};
const rememberScreen = (id) => {
  const prefix = id.substring(0, id.lastIndexOf("/"));
  ScreenHistory[prefix] = id;
};

/**
 * Hook that handles client-side routing and scroll restoration
 */
export default function useRoutingAndScroll() {
  /* ******** Router ******** */
  let [activeScreen, setActiveScreen] = useState(() => getScreenFromUrl());
  const screen = getScreen(activeScreen);
  /* INVARIANT: `screen` is valid and non-virtual */

  if (isDefaultScreen(activeScreen)) {
    // TODO: Seems like an odd place to handle this
    window.history.replaceState({ screenid: activeScreen }, activeScreen, "/");
  }
  if (!screen.hidden) rememberScreen(activeScreen);

  function switchScreen(e) {
    let screenid = null;
    if (typeof e === "number") {
      screenid = getScreens(screen)[e].id;
    } else {
      /* The links need to be `a` tags with `href` attribute
       * for the `react-snap` crawler to work,
       * but we don't want to issue requests here. */
      e.preventDefault();
      ({ screenid } = e.currentTarget.dataset);
    }
    const properId = resolveScreen(screenid, ScreenHistory).id;
    const { scrollTop } = rootElement;
    scrollHeights[properId] = scrollTop;
    const screenPath = isDefaultScreen(properId) ? "/" : `${properId}.html`;
    if (activeScreen !== properId) {
      window.history.pushState({ properId }, properId, screenPath);
      setActiveScreen(properId);
    }
  }
  // TODO: Use layout effect?
  useEffect(() => {
    // restore scroll
    if (activeScreen in scrollHeights) {
      rootElement.scroll({
        top: scrollHeights[activeScreen],
        behavior: "smooth",
      });
    }
    const goToPrevious = (event) => {
      scrollHeights[activeScreen] = rootElement.scrollTop;
      const id = getScreenFromState(event.state);
      // ASSERT isProperScreen(id)
      setActiveScreen(id);
    };
    // TODO: Can't we move the listener out and just set a value here?
    window.addEventListener("popstate", goToPrevious);
    return () => {
      window.removeEventListener("popstate", goToPrevious);
    };
  }, [activeScreen]);
  return [
    /* screen in `Screens` immutable singleton */
    screen,
    // /* switch screen, either use it as a DOM handler or pass an `ix` */
    switchScreen,
  ];
}

/* ******** Helpers for Routing/Scroll Hook ******** */

// always a valid screenid
const getScreenFromUrl = () => {
  const { pathname } = window.location;
  const ixDot = pathname.lastIndexOf(".");
  const screenid = ixDot === -1 ? pathname : pathname.substring(0, ixDot);
  return resolveScreenOrDefaultId(screenid);
};

const getScreenFromState = (state) =>
  state && state.screenid ? state.screenid : getScreenFromUrl();
