import React, { createElement as $ } from "react";
import styled from "styled-components";

import { addresse, telefon, email, telefonAsLink } from "../site/data";

import { legalScreens } from "../site/Screens";
import { ScreenLink } from "../components/Links/links";
import { overlap, Item } from "../styled/justify-content";
import {
  CopyrightIcon,
  HomeIcon,
  TelephoneIcon,
  EmailIcon,
} from "../styled/icons";
import { InlineTableLike, RowLike, CellLike } from "../styled/tables";

const IconTable = styled(InlineTableLike)`
  font-size: 125%;
`;

function Footer({ onSwitchScreen }) {
  const legalScreenLinks = legalScreens.map(({ id, title }) =>
    $(
      ScreenLink,
      {
        block: true,
        screenid: id,
        onFollow: onSwitchScreen,
      },
      title
    )
  );
  return (
    <InterleavedFooter>
      <Item as={IconTable} justify="center">
        <RowLike>
          <CopyrightIcon aria-label="Copyright" small />
          Italienisch Lernen – Debora Difonzo
        </RowLike>
        <RowLike>
          <HomeIcon aria-label="address" small />
          <CellLike>
            {addresse.strasse}
            <br />
            {addresse.ort}
          </CellLike>
        </RowLike>
        <RowLike>
          <TelephoneIcon small />
          <DiscreetLink href={`tel:${telefonAsLink}`}>{telefon}</DiscreetLink>
        </RowLike>
        <RowLike>
          <EmailIcon small />
          <DiscreetLink href={`mailto:${email}`}>{email}</DiscreetLink>
        </RowLike>
      </Item>
      {$(Item, null, ...legalScreenLinks)}
    </InterleavedFooter>
  );
}

export default Footer;

const DiscreetLink = styled.a`
  color: inherit;
  text-decoration: none;
`;

const InterleavedFooter = overlap(styled.div`
  /* margin-top: -83px; /* CALC: - .app--footer:height * */
  padding: 3ex;
  font-size: 80%;
  font-weight: 400;
  text-align: center;
  background-color: hsl(18, 43%, 20%);
  opacity: 0.85;
  color: #fff;
`);
