import styled from "styled-components";

// see https://github.com/styled-components/styled-components/issues/338#issuecomment-344838004

import { createElement as $ } from "react";

const InlineSvg = ({
  width,
  height /* = '1em', see below */,
  dim = 32,
  color = "currentColor",
  title,
  strokeBased = false,
  className,
  children,
}) =>
  $(
    "svg",
    {
      width:
        width || height
          ? width
          : "1em" /* if height is specified, don't use default (square) width! */,
      height,
      viewBox: Array.isArray(dim)
        ? `0 0 ${dim[0]} ${dim[1]}`
        : `0 0 ${dim} ${dim}`,
      fill: strokeBased ? undefined : color,
      fillOpacity: strokeBased ? 0 : undefined,
      stroke: strokeBased ? color : undefined,
      className,
    },
    title && $("title", null, title),
    // This seems to be a terrible idea: style rules apply globally! and so this rule is present multiple times on each svg item
    // $('style', null, `
    //     svg {
    //       vertical-align: middle;
    //       fill: ${color};
    //     }
    //   `),
    children
  );

export default styled(InlineSvg)`
  vertical-align: middle;
`;
