import React, { createElement as $ } from "react";
import styled, { css } from "styled-components";
import ExternalLinkIcon from "../../styled/ExternalLinkIcon";

/** *PROPS*
 *  * `screenid`: one of the keys in `Screens`
 *  * `onFollow`: callback
 */
export const ScreenLink = ({
  screenid,
  onFollow,
  block,
  neutral,
  ...props
}) => {
  const link = $(LinkStyle, {
    href: `${screenid}.html`,
    "data-screenid": screenid,
    onClick: onFollow,
    onKeyPress(e) {
      if (e.key === "Enter") onFollow(e);
    },
    block,
    neutral,
    ...props,
  });
  return block ? $("div", null, link) : link;
};

const LinkStyle = styled.a`
  ${({ block }) =>
    block &&
    css`
      display: inline-block;
    `}
  font-size: 112.5%;
  ${({ block }) =>
    !block &&
    css`
      margin: -0.5ex;
    `}
  padding: 0.5ex;
  cursor: pointer;
  color: ${({ block, neutral }) =>
    neutral ? "inherit" : css`hsl(214, 74%, ${block ? "77%" : "33%"})`};
  text-decoration-line: ${({ block }) => (block ? "none" : "underline")};
  :hover {
    background-image: ${({ block }) =>
      block
        ? css`linear-gradient(
       hsla(12, 88%, 87%, 0.125),
       hsla(12, 88%, 87%, 0.25))`
        : css`linear-gradient(
      rgba(64, 16, 4, 0.125),
      rgba(64, 16, 4, 0.25)
    )`};
  }
`;

const Link = LinkStyle;

export default Link;

export const ExternalLink = ({ children, ...props }) => (
  <Link {...props}>
    <ExternalLinkIcon css="margin-right: .5ex;" />
    {children}
  </Link>
);
