import React, { useRef } from "react";
import ImageGallery from "react-image-gallery";
import styled from "styled-components";
import thumb1 from "../../img/foto-ufficio/thumbs/ufficio01.png.jpg";
import thumb2 from "../../img/foto-ufficio/thumbs/ufficio02.png.jpg";
import thumb3 from "../../img/foto-ufficio/thumbs/ufficio03.png.jpg";
import thumb4 from "../../img/foto-ufficio/thumbs/ufficio04.png.jpg";
import thumb5 from "../../img/foto-ufficio/thumbs/ufficio05.png.jpg";
import thumb6 from "../../img/foto-ufficio/thumbs/ufficio06.png.jpg";
import thumb7 from "../../img/foto-ufficio/thumbs/ufficio07.png.jpg";
import thumb8 from "../../img/foto-ufficio/thumbs/ufficio08.png.jpg";
import thumb9 from "../../img/foto-ufficio/thumbs/ufficio09.png.jpg";
import thumb10 from "../../img/foto-ufficio/thumbs/ufficio10.png.jpg";
import thumb11 from "../../img/foto-ufficio/thumbs/ufficio11.png.jpg";
import thumb12 from "../../img/foto-ufficio/thumbs/ufficio12.png.jpg";
import thumb13 from "../../img/foto-ufficio/thumbs/ufficio13.png.jpg";
import thumb14 from "../../img/foto-ufficio/thumbs/ufficio14.png.jpg";
import thumb15 from "../../img/foto-ufficio/thumbs/ufficio15.png.jpg";
import thumb16 from "../../img/foto-ufficio/thumbs/ufficio16.png.jpg";
import thumb17 from "../../img/foto-ufficio/thumbs/ufficio17.png.jpg";
import image1 from "../../img/foto-ufficio/ufficio01.png.jpg";
import image2 from "../../img/foto-ufficio/ufficio02.png.jpg";
import image3 from "../../img/foto-ufficio/ufficio03.png.jpg";
import image4 from "../../img/foto-ufficio/ufficio04.png.jpg";
import image5 from "../../img/foto-ufficio/ufficio05.png.jpg";
import image6 from "../../img/foto-ufficio/ufficio06.png.jpg";
import image7 from "../../img/foto-ufficio/ufficio07.png.jpg";
import image8 from "../../img/foto-ufficio/ufficio08.png.jpg";
import image9 from "../../img/foto-ufficio/ufficio09.png.jpg";
import image10 from "../../img/foto-ufficio/ufficio10.png.jpg";
import image11 from "../../img/foto-ufficio/ufficio11.png.jpg";
import image12 from "../../img/foto-ufficio/ufficio12.png.jpg";
import image13 from "../../img/foto-ufficio/ufficio13.png.jpg";
import image14 from "../../img/foto-ufficio/ufficio14.png.jpg";
import image15 from "../../img/foto-ufficio/ufficio15.png.jpg";
import image16 from "../../img/foto-ufficio/ufficio16.png.jpg";
import image17 from "../../img/foto-ufficio/ufficio17.png.jpg";

const GalleryImage = styled.img`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
`;

const ImageAspectRatio = styled.div`
  position: relative;
  background-color: rgba(128, 128, 128, 0.15);
  border: 2px solid white; /* Workaround for react-image-gallery transform rounding errors (edge of adjacent images visible) */
  :after {
    display: block;
    padding-bottom: 134%; /* 800 x 600 ratio */
    content: "";
  }
`;

const Wrapper = styled.div`
  width: 60%;
`;

const BueroGallerie = ({ onHeightChange }) => {
  const IMAGES = [
    { original: image1, thumbnail: thumb1 },
    { original: image2, thumbnail: thumb2 },
    { original: image3, thumbnail: thumb3 },
    { original: image4, thumbnail: thumb4 },
    { original: image5, thumbnail: thumb5 },
    { original: image6, thumbnail: thumb6 },
    { original: image7, thumbnail: thumb7 },
    { original: image8, thumbnail: thumb8 },
    { original: image9, thumbnail: thumb9 },
    { original: image10, thumbnail: thumb10 },
    { original: image11, thumbnail: thumb11 },
    { original: image12, thumbnail: thumb12 },
    { original: image13, thumbnail: thumb13 },
    { original: image14, thumbnail: thumb14 },
    { original: image15, thumbnail: thumb15 },
    { original: image16, thumbnail: thumb16 },
    { original: image17, thumbnail: thumb17 },
  ].map(({ original, ...image }) => ({
    ...image,
    renderItem: () => (
      <ImageAspectRatio>
        <GalleryImage
          alt="Italienisch Lernen - Debora Difonzo. Büro"
          src={original}
          onLoad={() => {
            if (firstRun.current) {
              firstRun.current = false;
              onHeightChange();
            }
          }}
        />
      </ImageAspectRatio>
    ),
  }));
  const firstRun = useRef(true);

  return (
    <Wrapper>
      <ImageGallery items={IMAGES} />
    </Wrapper>
  );
};

export default BueroGallerie;
